body {
  margin: 0;
  background-color: #FAFAF8;
}

#root {
  font-family: Inter, sans-serif;
}

header {
  height: 80px;
  width: 100%;
  display: flex;
  align-items: center;
  background-color: white;
  box-shadow: 0px 1px 3px 0px rgba(0, 0, 0, 0.10), 0px 1px 2px 0px rgba(0, 0, 0, 0.06);
}

.navbar {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
  width: 100%;
}

.nav-logo-icon {
  height: 52px;
  width: 43px;
  margin-right: 1em;
}

.nav-logo-text {
  font-size: 2em;
}

.main {
  padding: 30px;
  display: flex;
  flex-direction: column;
}

.add-ingredient-form {
  display: flex;
  justify-content: center;
  gap: 12px;
  height: 38px;
}

.add-ingredient-form>input {
  flex-grow: 1;
  min-width: 150px;
  max-width: 400px;
  border-radius: 6px;
  border: 1px solid #D1D5DB;
  padding: 9px 13px;
  box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.05);
}

.add-ingredient-form>button {
  font-family: Inter, sans-serif;
  border: none;
  border-radius: 6px;
  background-color: #141413;
  color: #FAFAF8;
  width: 150px;
  font-size: 0.875rem;
  font-weight: 500;
}

.add-ingredient-form>button::before {
  content: "+";
  margin-right: 5px;
}

.ingredient-list {
  flex-grow: 1;
  min-width: 150px;
  max-width: 400px;
}

.get-recipe-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 8px;
  background: #F0EFEB;
  padding: 10px 28px;
}

.get-recipe-container h3 {
  font-size: 1.125rem;
  font-weight: 500;
  line-height: 24px;
}

.get-recipe-container p {
  color: #6B7280;
  font-size: 0.875rem;
  line-height: 20px;
}

.get-recipe-container button {
  border: none;
  border-radius: 6px;
  background: #D17557;
  box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.05);
  color: #FAFAF8;
  padding: 9px 17px;
  font-family: Inter, sans-serif;
  font-size: 0.875rem;
  cursor: pointer;
}